import React from 'react';
import './stall.css';
import {TextareaItem} from 'antd-mobile';
import { Picker, List, WhiteSpace } from 'antd-mobile';
import { DatePicker } from 'antd-mobile';

const nowTimeStamp = Date.now();
const now = new Date(nowTimeStamp);

class Stall extends React.Component {
    state = {
        data: [],
        cols: 1,
        asyncValue: [],
        date: now,
    };
    onChange3 = (val) => {
        console.log(val);
    };

    onChange4 = (val) => {
        console.log(new Date(val));
        var d = new Date(val);
        var  resDate = d.getFullYear() +
            '-' + (d.getMonth() + 1) +
            '-' + d.getDate()
        console.log(resDate)
    };

    onPickerChange = (val) => {

    };

    render() {
        return (
            <div className='stall'>
                <Picker
                    data={this.state.data}
                    cols={this.state.cols}
                    value={this.state.asyncValue}
                    onPickerChange={this.onPickerChange}
                    onOk={v => console.log(v)}
                >
                    <List.Item arrow="horizontal" onClick={this.onClick}>
                        
                    </List.Item>
                </Picker>

                <div className='p-stall'>
                    <p>2018-10-31</p>
                    <p className='date-p'>
                        <span></span>
                        <DatePicker
                            mode="date"
                            extra="Optional"
                            value={this.state.date}
                            onChange={this.onChange4}
                        >
                            <div className='input-stall'></div>
                        </DatePicker>
                        <span> 3 </span>
                    </p>
                    <p>12321 </p>
                    <p>131 </p>
                </div>
                <div className='TextareaItem'>
                    <TextareaItem
                        placeholder=''
                        rows={5}
                        count={100}
                        onChange={this.onChange3}
                    />
                </div>

                <div className='btn-repa-cint'>
                    <button className='btn-repa' onClick={this.go}></button>
                </div>
            </div>
        );
    }
}

export default Stall;
