import wx from 'weixin-js-sdk';
import api from './api';

export default {
    getWXSign(showLoading = true) {
        return new Promise((resolve, reject) => {
            api.postData('/api/WeChat/GetWeChatSign', {
            }, showLoading).then(data => {
                console.log('GetWeChatSign:', data.data)
                wx.config({
                    debug: false, // ,apialertpclogpc
                    appId: data.data.appId, // corpid
                    timestamp: data.data.timestamp, // 
                    nonceStr: data.data.nonceStr, // 
                    signature: data.data.signature, // 1
                    jsApiList: [
                        "scanQRCode",
                        "onMenuShareTimeline",
                        "onMenuShareAppMessage",
                        "onMenuShareQQ",
                        "getLocation",
                        "openLocation",
                        "WeixinJSBridge",
                        "chooseWXPay",
                        "showMenuItems",
                        "hideMenuItems",
                        "chooseImage",
                        "uploadImage",
                        "downloadImage",
                        "previewImage",
                        "getLocalImgData",
                        "hideAllNonBaseMenuItem"
                    ] // JSJS2
                });
                wx.ready(function () {
                    resolve(wx);
                });
                wx.error(function (res) {
                    console.log('error', res);
                    // configerrorconfigdebugresSPA
                });
            }).catch(e => {
                reject(e);
            });
        });
    }
};
