import {Toast} from "antd-mobile";


export default {
    /**
     * loading
     * @param msg loading "..."
     */
    showLoading(msg = "...") {
        Toast.loading(msg, 0);
    },
    /**
     * loading
     */
    hiddenLoading() {
        Toast.hide();
    },
    /**
     * 
     * @param msg 
     */
    showSuccess(msg) {
        Toast.success(msg,2);
    },
    /**
     * 
     * @param msg 
     */
    showError(msg) {
        Toast.info(msg);
    }
};
