export default {
    urlSearch(str) {
        let name, value;
        let num = str.indexOf('?');
        str = str.substr(num + 1); //   stringvar.substr(start [, length ]
        let arr = str.split('&'); //
        console.log(arr);
        let params = {};
        for (let i = 0; i < arr.length; i++) {
            num = arr[i].indexOf('=');
            if (num > 0) {
                name = arr[i].substring(0, num);
                value = arr[i].substr(num + 1);
                params[name] = value;
            }
        }
        return params;
    },
};
